<template>
    <v-container grid-list-lg id='pricing' align='center'>
        
        <v-layout row wrap>
    
            <v-flex xs12 sm12 md12>
                <v-card v-show='parcelIntro' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                            <v-tabs 
                            center-active
                            color='white'
                            v-model='selectedCategory'
                            class="mx-1"
                            icon>
                            <v-tab style='color:#fff'>България</v-tab>
                            <v-tab disabled style='color:#fff'>Международни</v-tab>
                            <v-tab disabled style='color:#fff'>Палетни</v-tab>
                            <v-tab disabled style='color:#fff'>Въздушни Експрес</v-tab>
                            </v-tabs>
                        </v-app-bar>
                    </v-card-title>
                    <v-layout row wrap justify-center>
                        <v-flex xs12 sm12 md3>
                            <v-select
                            v-model="service"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на доства"
                            hint="Вида зависи от начина на подаване и взимане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md3>
                            <v-select
                            v-model="parcel"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на пратката"
                            hint="В зависимост дали изпращане документи или продукт"
                            @change="documentPackage"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md3>
                            <v-text-field
                            v-model="weight"
                            background-color='white darken-3'
                            filled
                            :disabled='weightEnabled'
                            label="Тежест на пратката (в кг.)" 
                            hint="Тежеста силно зависи и от размера и обема на пратката"
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='officeService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="evropatOffices"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='officeAddressService' row wrap justify-center>
                        <v-flex xs12 sm12 md3>
                            <v-select
                            v-model="senderAddress"
                            :items="evropatOffices"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '100' }"
                            label="Офис изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md2>
                            <v-text-field
                            v-model="recipientCity"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '100' }"
                            label="Град"
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-text-field
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '100' }"
                            label="Адрес получател"
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressOfficeService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout row wrap justify-center>
                        <v-flex xs8 sm12 md3>
                            <v-btn
                            block
                            rounded
                            color="primary"
                            @click="checkPrice"
                        >
                            Покажи цена</v-btn
                        >
                        </v-flex>
                        <v-flex v-if='parcelPriceRecieved' xs12 sm12 md4 class='display-1 primary--text'>
                            {{parcelPrice}}
                        </v-flex>
                    </v-layout>

                    <v-expand-x-transition easein>
                    <v-layout v-if='parcelAlert' row wrap justify-center>
                        <v-flex xs8 sm12 md6>
                            
                                <v-alert
                                type="error"
                                >
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                     <v-card-text>
                        Вижте партньорскара ни офисна мрежа <a href='https://evropat.bg/offices/'>тук</a>!
                    </v-card-text>

                    <v-card-text>
                        Цените са крайни, без допълнителни такси за гориво или тол и са с включен ДДС.<br>
                        <strong>Калкулира се обемно тегло на пратката в случай че надвишава реалното по формула Д*Ш*В/6000</strong><br>
                        Пример: кашон с размери 50х50х50см - обемно тегло 50*50*50=125,000 / 6000 = 20,83кг <br>
                    </v-card-text>

                    <v-layout row wrap justify-center>
                        <v-flex xs8 sm12 md3>
                        <v-btn
                            rounded
                            block
                            color="primary"
                            @click="proceedDetails"
                        >
                            Напред</v-btn
                        >
                    </v-flex>
                    </v-layout>
                </v-card>

                <v-card v-show='parcelDetails' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                        {{parcel}} | Цена: {{parcelPrice}}
                        </v-app-bar>
                    </v-card-title>
                    <v-layout row wrap justify-center>
                        <v-flex xs12 sm12 md3>
                            <v-select
                            v-model="service"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на доства"
                            hint="Вида зависи от начина на подаване и взимане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md3>
                            <v-select
                            v-model="parcel"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на пратката"
                            hint="В зависимост дали изпращане документи или продукт"
                            @change="documentPackage"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md3>
                            <v-text-field
                            v-model="weight"
                            background-color='white darken-3'
                            filled
                            :disabled='weightEnabled'
                            label="Тежест на пратката (в кг.)" 
                            hint="Тежеста силно зависи и от размера и обема на пратката"
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='officeService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="evropatOffices"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='officeAddressService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="evropatOffices"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressOfficeService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressService' row wrap justify-center>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="senderAddress"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес изпращане"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                        <v-flex xs12 sm12 md4>
                            <v-select
                            v-model="recipientAddress"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес получаване"
                            persistent-hint
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-expand-x-transition easein>
                    <v-layout v-if='parcelAlert' row wrap justify-center>
                        <v-flex xs8 sm12 md6>
                                <v-alert
                                type="error"
                                >
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                    <!-- <v-layout v-if='parcelAlert' row wrap justify-center>
                        <v-flex xs8 sm12 md6>
                            <v-checkbox color='white'>
                            Съгласен съм
                            </v-checkbox>
                        </v-flex>
                    </v-layout> -->

                    <v-layout row wrap justify-center>
                        <v-flex xs8 sm12 md3>
                        <v-btn
                            rounded
                            block
                            color="primary"
                            @click='proceedDetails'
                        >
                            Плати</v-btn
                        >
                    </v-flex>
                    </v-layout>
                </v-card>
            </v-flex>
        </v-layout>
        
    </v-container>
</template>

<script>
import axios from 'axios';

export default {
    name: 'ParcelCreation',

    
    data: () => ({
        selectedCategory: '',
        officeDatabase: [],
        //
        service: '',
        parcel: '',
        weight: '',
        weightEnabled: false,
        //
        // senderOffice: '',
        // recipientOffice: '',
        senderCity: '',
        senderAddress: '',
        recipientCity: '',
        recipientAddress: '',

        //
        parcelAlert: false,
        parcelPriceRecieved: false,
        parcelPrice: 0,
        parcelIntro: true,
        parcelDetails: false,

        //
        typeOfService: ['Офис до офис', 'Офис до адрес', 'Адрес до офис', 'Адрес до адрес'],
        typeOfParcel: ['Документи', 'Пратка'],
        // typeWeight: ['до 1кг', 'от 1.001кг до 2кг', 'от 2.001кг до 3кг', 'от 3.001кг до 4кг',
        // 'от 4.001кг до 5кг', 'от 5.001кг до 6кг', 'от 6.001кг до 7кг', 'от 7.001кг до 8кг', 
        // 'от 8.001кг до 9кг', 'от 9.001кг до 10кг', 'от 10.001кг до 11кг', 'от 11.001кг до 12кг',],
        evropatOffices: [{}],
    }),

    computed: {
        officeService() {
            if(this.service=='Офис до офис') {
                return true
            } else {
                return false
            }
        },
        officeAddressService() {
            if(this.service=='Офис до адрес') {
                return true
            } else {
                return false
            }
        },
        addressOfficeService() {
            if(this.service=='Адрес до офис') {
                return true
            } else {
                return false
            }
        },
        addressService() {
            if(this.service=='Адрес до адрес') {
                return true
            } else {
                return false
            }
        },
    },

    methods: {
        documentPackage() {
            if(this.parcel=='Документи') {
                this.weight = '0.5'
                this.weightEnabled = true
            } else {
                this.weightEnabled = false
                this.weight = '0'
            }
        },

        async getOffices() {
            let payload = await axios.request('/parcel/get-offices', payload)
            let evropatOffices = []
            
            this.officeDatabase = payload.data[0].offices
            this.officeDatabase.forEach(function (item) {
                // console.log(item.officeName, index)
                evropatOffices.push(item.officeName)
            });
            evropatOffices.sort()
            this.evropatOffices = evropatOffices
        },

        async checkPrice() {

            let indexService = this.typeOfService.findIndex(object => {
                return object === this.service;
            });

            let indexType = this.typeOfParcel.findIndex(object => {
                return object === this.parcel;
            });

            this.destinationType = this.selectedCategory
            
            let indexSendAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.senderAddress;
            });

            let indexRespAddress = this.officeDatabase.findIndex(object => {
                return object.officeName === this.recipientAddress;
            });

            let payload = {
                'parcelType': indexType,
                'parcelWeight': this.weight,
                'destinationType': this.destinationType,
                'deliveryType': indexService,
                'senderAddress': this.officeDatabase[indexSendAddress].officeDestinationID,
                'recipientAddress': this.officeDatabase[indexRespAddress].officeDestinationID,
            }
            // console.log('Payload', payload)
            let request = await axios.post('/parcel/check-price', payload)
            console.log('Response', request)
            let parcelHolder = request.data[0].response[1].mainServicePrice
            this.parcelPrice = parcelHolder
            this.parcelPriceRecieved = true

            // if(request.data[0].status == 'success'){
            //     this.isRegister = false;
            //     this.errorMessage = "";
            // }  else if(request.data[0].status == 'emailExists') {
            //     this.errorMessage = "Email already exists!";
            // }
        },

        async sendParcel() {
            if(this.password == this.confirmPassword){

                let payload = {
                    'email': this.email,
                    'password': this.password,
                    'first_name': this.firstName,
                    'last_name': this.lastName,
                    'phone_number': this.phone_number
                }
                // console.log('Payload', payload)
                let request = await axios.post('/register', payload)
                // console.log('Response', request)

                if(request.data[0].status == 'success'){
                    this.isRegister = false;
                    this.errorMessage = "";
                }  else if(request.data[0].status == 'emailExists') {
                    this.errorMessage = "Email already exists!";
                }
                
            }
            else {
                this.errorMessage = "Passwords do not match!"
            }
        },

        proceedDetails() {
            this.parcelIntro = false
            this.parcelDetails = true
        }
    },

    mounted() {
        this.getOffices()
    },
};
</script>

